@import url(http://fonts.googleapis.com/css?family=Open+Sans);
html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
}

#root {
  height: 100%;
}

div {
  /*border: 1px solid red;*/
}

.App {
  font-family: "Open Sans";
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
}

#mapNav {
  background: rgb(22,22,22);
  color: grey;
  display: flex;
  flex-direction: row;
  padding-left: 20px;
  height: 40px;
  flex-shrink: 0;
}

#title {
  font-size: 11pt;
  color: lightgrey;
  font-style: italic;
  padding: 0 15px;
}

#mapNav p{
  cursor: pointer;
  margin: 10px 3px;
}

#selected {
  color: white;
}

.App-header {
  height: 200px;
  flex-grow: 0;
  flex-shrink: 0;
}

.App-content {
  flex-grow: 1;
  display: flex;
}

.App-filter {
  width: 200px;
  flex-grow: 0;
}

.App-main-content {
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 50px;
  flex-grow: 1;
  display: flex;
  overflow-x: auto;
  justify-content: center;
}
.Mem {
    display: flex;
    flex-flow: row wrap;
    overflow-x: auto;
    flex-grow: 1;
}

.Modal {
    display: flex;
    justify-content: center;
    flex-direction: column;
    outline: 0px;
    font-family: "Open Sans";
}

#modal {
    max-width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-self: center;
    outline: 0px;
}

.Arrows {
    align-self: center;
    padding: 10px 10px;
    color: white;
    cursor: pointer;
}

#modal-card {
    background: white;
    text-align: center;
    display: flex;
    flex-direction: column;
    max-height: 90%;
}

#detail_img {
    flex-shrink: 1;
    display: flex;
    justify-content: center;
    /*overflow-x: auto;*/
}

#modal-card img{
    object-fit: contain;
}

#modal-card p{
    flex-shrink: 0;
    max-width:600px;
    padding: 10px 20px;
    text-align: left;
    align-self: center;
}

#location svg{
    max-height: 15px;
    align-self: center;
    margin-left: 20px;
}
#location {
    flex-shrink: 0;
    font-style: italic;
    color: grey;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.mem-image {
    height: 200px;
    width: auto;
}
.mem-item {
    cursor: pointer;
    margin: 10px 5px;
}
.mem-title {
    padding-bottom: 5px;
}
.Guide {
    max-width: 80%;
    display: flex;
    flex-direction: column;
    overflow-x: auto;
}
.content {
    text-align: left;
    margin-bottom: 40px;
    line-height: 200%;
}

.Map {
    left: -100%
}
#activeSection {
    color: black;
    font-weight: bold;
}
#selection {
    font-weight: bold;
    color: grey;
}
#nav a span{
    color: grey;
}
a.ListItemLink.is-current{
    color: black;
}
a.ListItemLink{
    font-size: 10pt;
    color: grey;
}
.ListItem {
    padding: 0 20px !important;
    font-size: 10pt;
}
